
"use client";

import { useState, useTransition, useCallback } from "react";
import { MainHeader } from "@/components/common/main-header";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Loader, FileImage, FileVideo, FileText, Mic } from "lucide-react";
import { analyzeWitnessStatements, type AnalyzeWitnessStatementsOutput } from "@/ai/flows/analyze-witness-statements";
import { detectIdentifyObjects, type DetectIdentifyObjectsOutput } from "@/ai/flows/detect-identify-objects";
import { analyzeBloodstainPatterns, type AnalyzeBloodstainPatternsOutput } from "@/ai/flows/analyze-bloodstain-patterns";
import { reconstructCrimeScene3D, type ReconstructCrimeScene3DOutput } from "@/ai/flows/reconstruct-crime-scene-3d";
import { generateInvestigativeReport } from "@/ai/flows/generate-investigative-report";

type Step = "upload" | "processing" | "report";

// Helper to convert file to data URI
const toDataURI = (file: File): Promise<string> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = () => resolve(reader.result as string);
    reader.onerror = reject;
    reader.readAsDataURL(file);
  });
};

export default function Home() {
  const [step, setStep] = useState<Step>("upload");
  const [isPending, startTransition] = useTransition();

  const [witnessStatement, setWitnessStatement] = useState("");
  const [crimeSceneImage, setCrimeSceneImage] = useState<File | null>(null);
  const [bloodstainImage, setBloodstainImage] = useState<File | null>(null);

  const [report, setReport] = useState<string | null>(null);

  const handleGenerateReport = () => {
    startTransition(async () => {
      setStep("processing");

      let evidenceSummary = "";
      let aiReasoning = "";
      let confidenceScores = "";
      let suspectRankings = "No suspects provided.";
      let escapeRoutes = "No escape routes identified.";

      const analysisPromises = [];

      // 1. Analyze Witness Statement
      if (witnessStatement) {
        analysisPromises.push(
          analyzeWitnessStatements({ statement: witnessStatement }).then(output => {
            evidenceSummary += `Witness Statement Summary: ${output.summary}\n`;
            aiReasoning += `Statement Analysis: Identified entities - ${output.entities.join(", ")}. Potential contradictions - ${output.contradictions.join(", ")}.\n`;
            confidenceScores += `Deception Score (Statement): ${output.deceptionScore.toFixed(2)}\n`;
          })
        );
      }

      // 2. Analyze Crime Scene Image (Object Detection & 3D Reconstruction)
      if (crimeSceneImage) {
        const photoDataUri = await toDataURI(crimeSceneImage);
        analysisPromises.push(
          detectIdentifyObjects({ photoDataUri }).then(output => {
            const objectNames = output.map(o => o.name).join(", ");
            evidenceSummary += `Objects Detected at Scene: ${objectNames}\n`;
            aiReasoning += `Object Detection: The AI identified the following objects with varying confidence: ${output.map(o => `${o.name} (Confidence: ${o.confidence.toFixed(2)})`).join(', ')}\n`;
          })
        );
         analysisPromises.push(
          reconstructCrimeScene3D({ mediaDataUri: photoDataUri }).then(output => {
            evidenceSummary += `Scene Layout: ${output.sceneDescription}\n`;
            aiReasoning += `3D Reconstruction: The AI generated a spatial understanding of the scene.\n`;
          })
        );
      }
      
      // 3. Analyze Bloodstain Image
      if (bloodstainImage) {
        const photoDataUri = await toDataURI(bloodstainImage);
        analysisPromises.push(
          analyzeBloodstainPatterns({ photoDataUri }).then(output => {
            evidenceSummary += `Bloodstain Analysis: Weapon type inferred as ${output.weaponType}. Trajectory: ${output.trajectory}.\n`;
            aiReasoning += `Bloodstain Pattern Analysis: Based on the pattern, the weapon was likely a ${output.weaponType}. The trajectory suggests ${output.trajectory}.\n`;
          })
        );
      }
      
      await Promise.all(analysisPromises);
      
      const reportOutput = await generateInvestigativeReport({
        evidenceSummary,
        aiReasoning,
        confidenceScores,
        suspectRankings,
        escapeRoutes,
      });

      setReport(reportOutput.report);
      setStep("report");
    });
  };

  const handleStartOver = () => {
    setStep('upload');
    setWitnessStatement('');
    setCrimeSceneImage(null);
    setBloodstainImage(null);
    setReport(null);
  };

  return (
    <div className="flex min-h-screen w-full flex-col">
      <MainHeader title="Investigative Report Generator" />
      <main className="flex-1 p-4 md:p-8 flex items-center justify-center">
        <div className="w-full max-w-4xl">
          {step === "upload" && (
            <Card>
              <CardHeader>
                <CardTitle>1. Upload Evidence</CardTitle>
                <CardDescription>
                  Provide the case files. The AI will analyze them to generate a preliminary report.
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="grid gap-2">
                  <Label htmlFor="witness-statement"><Mic className="inline mr-2" /> Witness Statement</Label>
                  <Textarea
                    id="witness-statement"
                    placeholder="Paste or type the witness statement here..."
                    value={witnessStatement}
                    onChange={(e) => setWitnessStatement(e.target.value)}
                    rows={5}
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="crime-scene-image"><FileImage className="inline mr-2" /> Crime Scene Photo(s)</Label>
                  <Input 
                    id="crime-scene-image" 
                    type="file" 
                    accept="image/*"
                    onChange={(e) => setCrimeSceneImage(e.target.files?.[0] || null)}
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="bloodstain-image"><FileVideo className="inline mr-2" /> Bloodstain Pattern Photo(s)</Label>
                  <Input 
                    id="bloodstain-image" 
                    type="file" 
                    accept="image/*"
                    onChange={(e) => setBloodstainImage(e.target.files?.[0] || null)}
                  />
                </div>
                <Button onClick={handleGenerateReport} disabled={!witnessStatement && !crimeSceneImage && !bloodstainImage} className="w-full">
                  Generate Investigative Report
                </Button>
              </CardContent>
            </Card>
          )}

          {step === "processing" && (
            <div className="text-center">
              <Loader className="mx-auto h-12 w-12 animate-spin text-primary" />
              <h2 className="mt-4 text-2xl font-semibold">AI Analysis in Progress...</h2>
              <p className="mt-2 text-muted-foreground">
                Veritas AI is reconstructing the scene, analyzing evidence, and compiling the report.
              </p>
            </div>
          )}

          {step === "report" && (
             <Card>
              <CardHeader>
                <CardTitle>3. Investigative Report</CardTitle>
                <CardDescription>
                  This report is generated by Veritas AI based on the provided evidence.
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                 <div className="prose prose-sm dark:prose-invert max-w-none whitespace-pre-wrap font-code rounded-md border bg-secondary/50 p-4">
                  {report}
                </div>
                <Button onClick={handleStartOver} className="w-full" variant="outline">
                  Start New Investigation
                </Button>
              </CardContent>
            </Card>
          )}
        </div>
      </main>
    </div>
  );
}

    